.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.meme span{
  position: absolute;
  width: 100%;
  left: 0px;
  margin-top: 290px;
  font-size: 38px;
  color: white;
  text-shadow: 2px 0 0 #060607, -2px 0 0 #000, 0 2px 0 #070707, 0 -2px 0 #000, 1px 1px #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;

}

.meme span:nth-child(1) {
  
  margin-top: 60px;
}

.meme img{
  width: 100%;
}

body .meme{
  background-color: rgb(92, 91, 91);
}